import { v4 as uuidv4 } from "uuid";

const baseDocument = () => ({
  _id: "",
  name: "",
  desktopMainSlideshow: {
    slides: [
      // {
      // _id: "",
      // mainImage: false,
      // path: ""
      // }
    ],
    visible: false,
  },
  mainSlideshow: {
    slides: [
      // {
      // _id: "",
      // mainImage: false,
      // path: ""
      // }
    ],
    visible: false,
  },
  mainTextBlock: {
    text1: "",
    text2: "",
    text3: "",
    author: "",
    visible: false,
  },
  collectionBlocks: [
    {
      bounipun_collection: "",
      slideshow: [
        // {
        // _id: "",
        // mainImage: false,
        // path: ""
        // }
      ],
      text1: "",
      text2: "",
      text3: "",
      buttonText: "",
      visible: false,
      key: uuidv4(),
    },
  ],
  productListBlocks: [
    {
      productList: "",
      imageList: [
        // {
        // _id: "",
        // mainImage: false,
        // path: ""
        // }
      ],
      moodImage: "",
      text1: "",
      text2: "",
      buttonText: "",
      visible: false,
      key: uuidv4(),
    },
  ],
  bounipunLab: {
    heading: "",
    paragraph: "",
    image: "",
    buttonText: "",
    visible: false,
  },
  quote: {
    logo: "",
    heading: "",
    paragraph: "",
    author: "",
    visible: false,
  },
  press: {
    logo: "",
    imageList: [
      //     {
      //     path: "",
      //     mainImage: false
      // }
    ],
    captions: [
      {
        heading: "",
        paragraph: "",
        key: uuidv4(),
      },
    ],
    visible: false,
  },
  instagram: {
    text: "",
    visible: false,
  },
  description: "",
  status: false,
});

export default {
  /* close form */
  /* slideshow list updated (x6) */
  /* add unique key to every block */
  /* fetch allCollections  */
  props: {
    model: String,
  },
  data() {
    return {
      editMode: false,
      doc: baseDocument(),
      allCollections: [],
      allProductLists: [],
      loading: false,
      updated: false,
    };
  },
  mounted() {
    this.fetchAllCollections();
    this.fetchAllProductLists();
  },
  methods: {
    /* set image list */
    imageListUpdated(list, property, index) {
      switch (property) {
        /* multiple */
        case "desktopMainSlideshow": {
          let { slides } = this.doc.desktopMainSlideshow;
          this.doc.desktopMainSlideshow.slides = list.map((item) => ({
            ...item,
            link: slides.find((s) => s._id === item._id)?.link || "",
          }));
          break;
        }
        /* multiple */
        case "mainSlideshow":
          let { slides } = this.doc.mainSlideshow;
          this.doc.mainSlideshow.slides = list.map((item) => ({
            ...item,
            link: slides.find((s) => s._id === item._id)?.link || "",
          }));
          break;
        /* multiple [indexed] */
        case "collectionBlock":
          this.doc.collectionBlocks[index].slideshow = list;
          break;
        /* multiple [indexed] */
        case "productListBlock":
          this.doc.productListBlocks[index].imageList = list;
          break;
        case "productListBlockMood":
          this.doc.productListBlocks[index].moodImage =
            list.length > 0 ? list[0].path : "";
          break;
        /* single */
        case "bounipunLab":
          this.doc.bounipunLab.image = list.length > 0 ? list[0].path : "";
          break;
        /* single */
        case "quote":
          this.doc.quote.logo = list.length > 0 ? list[0].path : "";
          break;
        /* single */
        case "pressLogo":
          this.doc.press.logo = list.length > 0 ? list[0].path : "";
          break;
        /* multiple */
        case "press":
          this.doc.press.imageList = list;
          /* update captions list */
          break;
      }
    },
    /* add caption box */
    addNewCaptionBox() {
      this.doc.press.captions.push({
        heading: "",
        caption: "",
        key: uuidv4(),
      });
      this.$forceUpdate();
    },
    removeCaption(key) {
      if (this.doc.press.captions.length < 2) return;
      this.doc.press.captions.splice(key, 1);
      this.$forceUpdate();
    },
    async fetchAllCollections() {
      const result = await this.$fetchCollection("collections");
      this.allCollections = result.docs.map(({ _id, name }) => {
        return {
          name,
          value: _id,
        };
      });
      this.allCollections.unshift({
        name: "Select Bounipun Collection",
        value: "",
      });
    },
    async fetchAllProductLists() {
      const result = await this.$fetchCollection("product_lists");
      this.allProductLists = result.docs.map(({ _id, name }) => {
        return {
          name,
          value: _id,
        };
      });
      this.allProductLists.unshift({
        name: "Select Product List",
        value: "",
      });
    },
    /* add collection block */
    addCollectionBlock() {
      this.doc.collectionBlocks.push({
        bounipun_collection: "",
        slideshow: [
          {
            _id: "",
            mainImage: false,
            path: "",
          },
        ],
        text1: "",
        text2: "",
        text3: "",
        buttonText: "",
        visible: false,
        key: uuidv4(),
      });
    },
    /* add product list block */
    addProductListBlock() {
      this.doc.productListBlocks.push({
        productList: "",
        imageList: [
          {
            _id: "",
            mainImage: false,
            path: "",
          },
        ],
        moodImage: "",
        text1: "",
        text2: "",
        buttonText: "",
        visible: false,
        key: uuidv4(),
      });
    },
    /* remove collection or product list block */
    removeBlock(property, key) {
      if (this.doc[property].length < 2) return;
      this.doc[property].splice(key, 1);
    },
    closeForm() {
      this.resetForm();
      this.$emit("close");
    },
    resetForm() {
      // this.$refs.desktopMainSlideshow.clearFileSelection();
      this.$refs.mainSlideshow.clearFileSelection();
      this.doc = baseDocument();
      console.log("doc was cleared");
    },
    async updateDocument() {
      // return;

      this.loading = true;
      const result = await this.$updateDocument(
        this.model,
        this.doc,
        this.editMode
      );
      this.loading = false;

      if (!result.updated) return;

      this.$emit("updated");
      // this.populateForm(result.doc);
      this.$flash(this);
    },
    populateForm(details) {
      const {
        _id,
        name,
        desktopMainSlideshow,
        mainSlideshow,
        mainTextBlock,
        collectionBlocks,
        productListBlocks,
        bounipunLab,
        quote,
        press,
        instagram,
        description,
        status,
      } = details;
      this.doc = {
        _id,
        name,
        desktopMainSlideshow,
        mainSlideshow,
        mainTextBlock,
        collectionBlocks,
        productListBlocks,
        bounipunLab,
        quote,
        press,
        instagram,
        description,
        status,
      };
      this.editMode = true;
    },
    async deleteDocument() {
      this.loading = true;
      const result = await this.$deleteDocument(this.model, this.doc._id);
      this.loading = false;

      if (!result.deleted) return;

      this.$emit("updated");
      this.resetForm();
      this.$flash(this);
    },
  },
};
